import React, { useState } from "react";
import { useHistory } from "react-router-dom";
import { ThemeProvider } from "styled-components";
import PriceBadge from "./price-badge";
import Details from "./details";
import { Card, CardTitle, SoldBadge, ImagePlaceholder } from "./car-card";

const CarListItem = (props) => {
  const [isLoading, setLoading] = useState(true);
  const car = props.car;
  const { title, price, id, sold } = car;

  const mileage = car.attributes.kms;
  const year = car.attributes.year.value;
  const fuelType = car.attributes.fuelType.value;

  const imgSrc = `/img/${id}/thumbnail.webp`;

  const history = useHistory();

  const handleLoad = () => {
    setLoading(false);
  };

  const getImageClasses = () => {
    let c = "";
    c += sold ? "bw" : "";
    c += isLoading ? "loading" : "";
    return c;
  };

  return (
    <ThemeProvider theme={props.theme.card}>
      <Card onClick={() => history.push(`/carro/${id}`)}>
        {isLoading && <ImagePlaceholder></ImagePlaceholder>}

        <img
          src={imgSrc}
          alt="car"
          className={getImageClasses()}
          onLoad={handleLoad}
        />

        <SoldBadge hidden={!sold}>
          <strong>Vendido</strong>
        </SoldBadge>

        <CardTitle className="ml-1 mr-1">{title}</CardTitle>

        <Details year={year} mileage={mileage} fuelType={fuelType} />

        <PriceBadge badge={props.theme.badge} price={price} sold={sold} />
      </Card>
    </ThemeProvider>
  );
};

export default CarListItem;
